@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&family=Roboto+Condensed:wght@300;400;700&display=swap);
:root {
  --primary-dark: #5651e5;
  --primary-light: #709dff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Poppins", sans-serif;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
}
h2 {
  font-size: 2rem;
}

.container {
  max-width: 1240px;
  margin: auto;
  padding: 0 1rem;
}

button {
  background-image: linear-gradient(45deg,#5651e5 0%, #709dff 100%);
  background-image: linear-gradient(45deg,var(--primary-dark) 0%, var(--primary-light) 100%);
  border: 1px solid #5651e5;
  border: 1px solid var(--primary-dark);
  color: #fff;
  padding: .7rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-family: 'Poppins', sans-serif;
}



@media screen and (max-width: 940px) {
  h1 {
    font-size: 2.5rem;
  }
  h2 {
    font-size: 1.5rem;
  }
}



.about {
  width: 100%;
  text-align: center;
  padding: 4rem 0;
}

.about h1 {
  padding: 1rem;
}

.img-container {
  --gap: 1rem;
  --num-cols: 5;
  --row-height: 300px;

  display: grid;
  grid-template-columns: repeat(var(--num-cols), 1fr);
  margin: auto;
  padding: 1rem 0;
  grid-gap: var(--gap);
  gap: var(--gap);
}
.seal {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 5px;
  -webkit-column-gap: 5px;
          column-gap: 5px;
}

.about img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.seal img {
  width: 100%;
  height: 100%;
}

.span-3 {
  grid-column: span 3;
}

.image-grid-row-2 {
  grid-row: span 2;
}

@media screen and (max-width: 940px) {
  .img-container {
    --num-cols: 2;
    --row-height: 200px;
    --grap: 0.5rem;
  }

  .span-3 {
    grid-column: span 2;
  }

 
}

